import { mapGetters } from 'vuex'

export default {
  data() {
    return {
      items: null,
    }
  },
  computed: {
    ...mapGetters({
      articles: 'articles/getArticles',
    }),
  },
  async mounted() {
    const data = await this.articles
    this.items = data.filter((el) => el.slider)
  },
  methods: {
    returnSource(src) {
      return `/images/${src}`
    },
  },
}
